.App {
  text-align: center;
  color: #255155;
  background-color: #F0F9FF;
  height: 100vh;
  overflow-y: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Header {
  background-color: #6DCFD9;
  min-height: 10px;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: left;
  float: left;
  justify-content: center;
  font-size: 50px;
  color: white;
}

.App-link {
  color: #61dafb;
}


.Footer{
  background-color: #70CDD7;
  min-height: 60px;
  width: 100%;
  height: 100%;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 25px;
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
